<template>
  <v-container class="max-width-container" fluid>
    <v-card class="rounded-lg">
      <v-card-title class="display-1 primary--text"
        ><b>DASHBOARD</b>
      </v-card-title>
      <v-card-text>
        <v-card v-if="specialties.length != 0" class="rounded-lg">
          <v-card-title><b>Mis Especialidades</b> </v-card-title>
          <v-card-text>
            <div>
              <v-sheet class="mx-auto" elevation="1">
                <v-slide-group class="pa-4" active-class="success" show-arrows>
                  <v-slide-item
                    v-for="(item, index) in specialties"
                    :key="index"
                    v-slot="{ active, toggle }"
                  >
                    <v-card
                      class="mx-auto ma-4"
                      :color="active ? undefined : undefined"
                      @click="toggle"
                      max-width="400"
                    >
                      <v-img
                        src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
                        height="200px"
                      ></v-img>

                      <v-card-title> {{ item.specialityName }} </v-card-title>

                      <v-card-subtitle>
                        CICLO {{ item.academicCycleName }}
                      </v-card-subtitle>
                    </v-card>
                  </v-slide-item>
                </v-slide-group>
              </v-sheet>
            </div>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  name: "DashboardDashboard",
  data() {
    return {
      specialties: [],
    };
  },
  created() {
    this.loadSpecialties();
  },
  methods: {
    async loadSpecialties() {
      await this.$axios
        .get("/enrollment-speciality/my", {
          headers: {
            Authorization: `Bearer ${localStorage.token}`,
          },
        })
        .then((response) => {
          this.specialties = response.data.allItems;
        })
        .catch((error) => {
          this.$queryError(error);
        });
    },
  },
};
</script>