<template>
  <div>
    <v-stepper v-model="e6" vertical>
      <v-stepper-step :complete="e6 > 1" step="1">
        Seleccionar Especialidad
      </v-stepper-step>

      <v-stepper-content step="1">
        <v-card color="grey lighten-5" class="mb-12">
          <v-card-text>
            <v-select
              v-model="selectedSpecialty"
              :items="specialties"
              item-text="specialityName"
              item-value="id"
              label="Especialidad"
              hide-details
              outlined
            ></v-select>
          </v-card-text>
        </v-card>
        <v-btn color="info" :disabled="selectedSpecialty == ''" @click="e6 = 2"
          ><b>CONTINUAR</b></v-btn
        >
      </v-stepper-content>

      <v-stepper-step :complete="e6 > 2" step="2">
        Subir Imagen de Voucher
      </v-stepper-step>

      <v-stepper-content step="2">
        <v-card color="grey lighten-5" class="mb-12">
          <v-card-text>
            <v-file-input
              v-model="selectedImage"
              accept="image/*"
              label="Seleccione una imagen"
              prepend-icon="mdi-camera"
              @change="previewImage"
              hide-details
              outlined
            ></v-file-input>
            <br>
            <v-row>
              <v-col lg="6" sm="12">
                <div v-if="selectedImage != null">
                  <h2>Imagen de Voucher</h2>
                  <v-img
                    class="mt-5"
                    v-if="previewURL"
                    :src="previewURL"
                    height="400"
                    contain
                  ></v-img>
                </div>
              </v-col>
              <v-col lg="6" sm="12">
                <v-form>
                  <div v-if="selectedImage != null">
                    <h2>Información del Voucher</h2>
                    <v-text-field
                      class="mt-5"
                      label="Ingrese Fecha de pago"
                      type="date"
                      outlined
                      clearable
                      required
                    ></v-text-field>
                    <v-select
                      :items="[
                        { id: 300000000000001, name: 'BCP' },
                        { id: 300000000000002, name: 'Banco de la Nación' },
                      ]"
                      item-text="name"
                      item-value="id"
                      label="Seleccione Banco"
                      outlined
                    ></v-select>
                    <v-text-field
                      label="Ingrese Número de operación"
                      type="number"
                      outlined
                      clearable
                      required
                    ></v-text-field>
                    <v-text-field
                      label="Monto de Voucher"
                      type="number"
                      outlined
                      clearable
                      required
                    ></v-text-field>
                    <v-text-field
                      label="Descripción"
                      type="text"
                      outlined
                      clearable
                      required
                    ></v-text-field>
                  </div>
                </v-form>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <v-btn
          color="info"
          @click="
            e6 = 3;
            loadPayments();
          "
          ><b>CONTINUAR</b></v-btn
        >
        <v-btn color="primary" text @click="e6 = 1"><b>VOLVER</b></v-btn>
        <!-- Prueba de subida archivo boton 
        <v-btn text @click="uploadFile()"><b>SUBIR IMAGEN</b></v-btn>-->
      </v-stepper-content>

      <v-stepper-step :complete="e6 > 3" step="3">
        Seleccionar datos del pago
      </v-stepper-step>

      <v-stepper-content step="3">
        <v-card color="grey lighten-5" class="mb-12">
          <v-card-text>
            <v-text-field
              label="Monto Pago de Matrícula"
              type="number"
              outlined
              clearable
              required
            ></v-text-field>
            <v-btn color="primary" class="mb-3" @click="number += 1"
              ><b>+ AÑADIR TIPO DE PAGO</b></v-btn
            >
            <v-row v-for="n in number" :key="n">
              <v-col>
                <v-select
                  :items="payments.allReasonPension"
                  item-text="reference"
                  item-value="id"
                  label="Pago de Pensión"
                  hide-details
                  outlined
                ></v-select>
              </v-col>
              <v-col>
                <v-text-field
                  label="Monto Pago de Pensión"
                  type="number"
                  outlined
                  clearable
                  hide-details
                  required
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <v-btn color="info" @click="e6 = 4"><b>CONTINUAR</b></v-btn>
        <v-btn color="primary" text @click="e6 = 2"><b>VOLVER</b></v-btn>
      </v-stepper-content>

      <v-stepper-step step="4"> Verificar información llenada</v-stepper-step>
      <v-stepper-content step="4">
        <v-card color="grey lighten-5" class="mb-12"></v-card>
        <v-btn color="success" @click="finalizar()"><b>FINALIZAR</b></v-btn>
        <v-btn color="primary" text @click="e6 = 3"><b>VOLVER</b></v-btn>
      </v-stepper-content>
    </v-stepper>
  </div>
</template>
  <script>
import router from "@/router";
export default {
  data() {
    return {
      e6: 1,
      selectedSpecialty: "",
      specialties: [],
      payments: {},
      number: 0,

      selectedImage: null,
      previewURL: null,

      // Prueba subida archivo
      file: null,
    };
  },
  created() {
    this.loadSpecialties();
  },
  methods: {
    async uploadFile() {
      // Crea una instancia de FormData
      var formData = new FormData();
      formData.append("file", this.file);

      // Realiza la solicitud POST utilizando axios u otra biblioteca de tu elección
      await this.$axiosFile
        .post("/storage/upload", formData, {
          headers: {
            Authorization: `Bearer ${localStorage.token}`,
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    // PRUEBA fin

    async loadSpecialties() {
      await this.$axios
        .get("/enrollment-speciality/my", {
          headers: {
            Authorization: `Bearer ${localStorage.token}`,
          },
        })
        .then((response) => {
          this.specialties = response.data.allItems;
        })
        .catch((error) => {
          this.$queryError(error);
        });
    },
    async loadPayments() {
      await this.$axios
        .get(
          "/enrollment-speciality/" +
            this.selectedSpecialty +
            "/payment-reason",
          {
            headers: {
              Authorization: `Bearer ${localStorage.token}`,
            },
          }
        )
        .then((response) => {
          this.payments = response.data;
        })
        .catch((error) => {
          this.$queryError(error);
        });
    },
    previewImage(file) {
      // Obtiene el valor de la imagen para enviarla
      this.file = file;

      // Mostrar imagen en formulario
      if (this.selectedImage) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.previewURL = e.target.result;
        };
        reader.readAsDataURL(this.selectedImage);
      }
    },

    finalizar() {
      this.$toast.success("Pago realizado satisfactoriamente");
      router.push({ name: "dashboard" });
    },
  },
};
</script>